import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.html.button
import kotlinx.html.dom.create
import kotlinx.html.js.div
import kotlinx.html.js.onClickFunction
import kotlinx.html.style
import org.w3c.dom.HTMLDivElement
import org.w3c.dom.HTMLElement
import org.w3c.dom.events.Event


class Keyboard(){
    /** This is a widget that puts keyboard on the screen depending on the given coordinates
     *  Everything is relative so technically it should fit the size of the box its in
     */
    var recordedWord = ""
    var letterPressed = {s: Char ->}
    var enterPressed = {}
    var erasePressed = {}
    val heightToWidth = 200.0/600.0 // height/width

    fun expectedHeight(width: Int): Int {
        return (width*heightToWidth).toInt()
    }

    fun generate(width: Int): HTMLDivElement {
        val height = expectedHeight(width)

        val buttonText = listOf("QWERTYUIOPĞÜ","ASDFGHJKLŞİ","1ZXCVBNMÖÇ2")
        val buttonVals = listOf("qwertyuıopğü","asdfghjklşi","1zxcvbnmöç2")
        val buttonLengths = buttonText.map {
            it.map{
                if(it=='1'||it=='2'){
                    1.5
                }else{
                    1.0
                }
            }
        }
        val rowLengths = buttonLengths.map {
            it.sum()
        }
        val topRowLength = rowLengths.maxOf { it }
        val intLengths = buttonLengths.map { bl->
            bl.map {
                (it/topRowLength*100.0).toInt()
            }
        }
        val intRowLengths = intLengths.map {
            it.sum()
        }


        val keyboardElement = document.create.div {
            style  = """
            height: ${toPx(height)};
            width: ${toPx(width)};
        """
        }.also {
            it.className = "keyboardElement"
        }

        //val defaultWidth = 100 // this will grow up and become percentile



        val rowHeight = height/3
        buttonText.indices.forEach { i->
            val buttonRow = document.create.div {
                style = """
                height: ${toPx(rowHeight)};
                clear: both;                                
            """
            }
            val marginLength = (100-intRowLengths[i])/2
            document.create.div {
                style = """
                height:${toPx(rowHeight)};
                width: ${marginLength}%;
                float: left;
            """
            }.also {
                buttonRow.append(it)
            }
            for (j in  buttonText[i].indices ){
                val buttonHole = document.create.div {
                    style = """
                    height: ${toPx(rowHeight)};
                    width: ${intLengths[i][j]}%;
                    float: left;
                """
                }
                document.create.button{
                    val thisText  =buttonText[i][j].toString()
                    if(thisText=="1"){
                        text("DENE")
                        onClickFunction = {event->
                            enterEvent(event)
                        }
                    }else if(thisText=="2"){
                        text("SİL")
                        onClickFunction = {event->
                            eraseEvent(event)
                        }
                    }else{
                        text(thisText)
                        onClickFunction = {event->
                            keyboardClick(event,buttonVals[i][j])
                        }
                    }

                }.also {
                    it.className = "keyboardButton"
                    it.id = "button_${buttonVals[i][j].toString()}"
                    buttonHole.append(it)
                }


                buttonRow.append(buttonHole)
            }
            keyboardElement.append(buttonRow)
        }
        return keyboardElement
    }

    fun resetKeys(){
        "qwertyuıopğüasdfghjklşizxcvbnmöç2".forEach {
            (document.getElementById("button_${it}")!! as HTMLElement).style.backgroundColor = ""
        }
    }

    fun paintKeys(greenList: List<Int>, yellowList: List<Int>, record: String){
        for (i in record.indices){
            val targetId = "button_${record[i]}"
            val targetKey = (document.getElementById(targetId)!! as HTMLElement)
            val currentColour = targetKey.style.backgroundColor
            if(currentColour == "rgb(10, 200, 10)"){
                continue
            }
            if (i in greenList){
                targetKey.style.backgroundColor = "rgb(10, 200, 10)"
                continue
            }
            if(currentColour=="rgb(200, 200, 10)"){
                continue
            }
            if (i in yellowList){
                targetKey.style.backgroundColor = "rgb(200, 200, 10)"
                continue
            }
            targetKey.style.backgroundColor = "rgb(100, 100, 100)"
        }
    }



    fun keyboardClick(event: Event, c: Char) {
        letterPressed(c)
        return Unit
    }

    fun enterEvent(event: Event){
        enterPressed()
    }

    fun eraseEvent(event: Event){
        erasePressed()
    }
}

