import kotlinx.browser.document
import kotlinx.browser.window
import org.w3c.dom.HTMLElement


fun resizeFunc(){
    val innerRect = InnerRect(window.innerWidth, window.innerHeight)
    val d = (document.getElementById("mainRectangle") as HTMLElement)

    d.style.also {
        it.width = toPx(innerRect.mainRect.width)
        it.height = toPx(innerRect.mainRect.height)
    }
}